a {
  border-bottom: solid 1px currentcolor;
  color: blue;
  text-decoration: none;

  &:hover {
    border-bottom: none;
  }

  &:visited {
    color: blue;
  }
}

blockquote {
  margin-bottom: 1rem;
  margin-left: 4rem;
  margin-right: 7rem;
  margin-top: 1.5rem;
}

cite {
  display: inline-block;
  margin-left: 4rem;
  margin-bottom: 1rem;
}

details {
  border: dotted 2px var(--black);
  padding: 0.5em;

  summary {
    cursor: pointer;

    & > * {
      display: inline;
    }
  }
}

figcaption {

  // font-sm
  font-size: 17px;
  letter-spacing: 0.015em;
  line-height: 1.3;
}

figure {
  margin: 2rem;
}

h1 {
  margin-top: 3rem;

  // font-lg
  font-size: 30px;
  letter-spacing: 0.025em;
  line-height: 1.3;
}

h2 {
  margin-top: 3rem;

  // font-lg
  font-size: 30px;
  letter-spacing: 0.025em;
  line-height: 1.3;
}

h3 {
  font-style: italic;
}

hr {
  border: none;
  border-bottom: solid 1px var(--black);
  margin-top: 2rem;
  margin-bottom: 2rem;
}

img {
  max-width: 100%;
}

li {
  max-width: 30em;
  margin-bottom: 0.5em;

  // Second-level list
  ul {
    margin-top: 1em;
  }
}

ol {
  padding-left: 1.25em;
}

p {
  margin-bottom: 1rem;
  margin-top: 1rem;
}

table {
  border: 1px solid var(--black);
  border-collapse: collapse;
  width: 100%;
}

td {
  border: 1px solid var(--black);
  padding: 8px;
  text-align: left;
  vertical-align: top;
  width: 25%;
}

th {
  border: 1px solid var(--black);
  padding: 8px;
  text-align: left;
  vertical-align: top;
}

tr {
  border-top: solid 1px var(--black);
}

ul {
  padding-left: 1em;
}
